import React, { useState } from 'react';
import './Contact.css';
import { useSpring, animated } from 'react-spring';
import { Transition } from 'react-spring/renderprops';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import Fade from '@material-ui/core/Fade';
import GitHubIcon from '@material-ui/icons/GitHub';
import EmailRoundedIcon from '@material-ui/icons/EmailRounded';
import DraftsRoundedIcon from '@material-ui/icons/DraftsRounded';
import DescriptionRoundedIcon from '@material-ui/icons/DescriptionRounded';
import InsertDriveFileRoundedIcon from '@material-ui/icons/InsertDriveFileRounded';

function Contact(props) {
  const [point, setPoint] = useState(0);
  const [click, setClick] = useState(0);
  const [cvPoint, setCvPoint] = useState(0);
  const [emailPoint, setEmailPoint] = useState(0);
  const [gitHubPoint, setGitHubPoint] = useState(0);

  const topShade = useSpring({
    boxShadow: point ? '0 10px 20px 0px rgba(0, 0, 0, 0.19)' : '0 0px 0px 0px white',
  });

  const topBlur = useSpring({
    filter: click ? 'blur(8px)' : 'blur(0px)',
    opacity: click ? 0.8 : 1,
  });

  const bot = useSpring({
    opacity: click ? 1 : 0,
    zIndex: click ? 1 : -1, 
  });

  return (
    <div id="contact">
      <h1 className="contact-header">Contact</h1>
      <hr />
      <div 
        className="contact-container"
        onMouseEnter={() => {setPoint(1)}}
        onMouseLeave={() => {setPoint(0)}}
      >
        <div className="contact-card">
          <div className="contact-card-content">
            <p>
              The best way to reach me is via email. 
              If you're a recruiter, take a look at my CV and GitHub.
            </p>
            <div className="icon-list">
              <CustomIconButton 
                href="mailto:tayjordan25@gmail.com" 
                title="Send me an Email"
                iconBefore={<EmailRoundedIcon />}
                iconAfter={<DraftsRoundedIcon />}
              />
              <CustomIconButton 
                href={require('../documents/CV.pdf')}
                title="CV"
                iconBefore={<InsertDriveFileRoundedIcon />}
                iconAfter={<DescriptionRoundedIcon />}
              />
              <CustomIconButton 
                href="https://github.com/Jordan-Tay" 
                title="GitHub"
                iconBefore={<GitHubIcon />}
                iconAfter={<GitHubIcon />}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

const CustomIconButton = prop => {
  const [point, setPoint] = useState(0);
  return (
  <a 
  href={prop.href}
  className="circular-link"
  target="_blank" 
  rel="noopener noreferrer"
    >
      <Tooltip 
        title={<span className="button-tooltip">{prop.title}</span>}
        TransitionComponent={Fade} 
        TransitionProps={{ timeout: 600 }}
      >
        <IconButton
          className="icon-button"
          onMouseEnter={() => setPoint(1)}
          onMouseLeave={() => setPoint(0)}
        >
          <Transition
            items={point}
            from={{ position: 'absolute', opacity: 0, margin: '-25%' }}
            enter={{ opacity: 1 }}
            leave={{ opacity: 0 }}
          >
            {point => 
                point
                  ? props => <div>{React.cloneElement(prop.iconAfter, {style:props})}</div>
                  : props => <div>{React.cloneElement(prop.iconBefore, {style:props})}</div>
            }
          </Transition>
        </IconButton>
      </Tooltip>
    </a>
  );
}

  export default Contact;
