import React, { useState } from 'react';
import './About.css';
import { useSpring, animated } from 'react-spring';

function About() {
  const [point, setPoint] = useState(0);

  const shade = useSpring({
    boxShadow: point ? '0 10px 20px 0px rgba(0, 0, 0, 0.19)' : '0 0px 0px 0px white',
  });

  return (
    <div id="about-me">
      <h1 className="about-header">About Me</h1>
      <hr />
      <div className="about-container">
        <div
          style={shade}
          className="about-card"
          onMouseEnter={() => setPoint(1)}
          onMouseLeave={() => setPoint(0)}
        >
          <div className="about-card-content">
            <h1>Hello!</h1> 
            <hr />
            <p>
              My name is Jordan Tay, and I'm from Kuala Lumpur, Malaysia. 
              I'm an aspiring software engineer and a second year BEng
              Computing student at Imperial College London.
            </p>
            <p>
              I started programming in pre-university, back when my focus was
              on competitive programming with C++. My interest eventually 
              expanded to building web applications, and I started dabbling 
              with Javascript and the p5.js library. In my more recent projects,
              I've explored newer frameworks including React and Express.
            </p>
            <p>
              I like reading articles, and keeping up to date with the tech 
              industry. I'm quite keen on AI and machine learning, seeing as 
              how it's implemented in so many of the products we use. I hope
              to explore in more detail and implement an AI into a project in
              the near future.
            </p>
            <p>
              Outside of computing, I enjoy playing chess, table tennis, as well
              as playing the piano.
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default About;
